import React, { useEffect, useRef } from 'react';
import { useApolloClient, useMutation } from '@apollo/react-hooks';
import { Layout, Card, Typography, Spin } from 'antd';
import { Redirect } from 'react-router-dom';

import { Viewer } from '../../lib/types';
import { AUTH_URL } from '../../lib/graphql/queries/AuthUrl';
import { LOG_IN } from '../../lib/graphql/mutations/LogIn';
import { AuthUrl as AuthUrlData } from '../../lib/graphql/queries/AuthUrl/__generated__/AuthUrl';
import { LogIn as LogInData, LogInVariables } from '../../lib/graphql/mutations/LogIn/__generated__/LogIn';
import { ErrorBanner } from '../../lib/components';
import { displaySuccessNotification, displayErrorMessage } from '../../lib/utils';
import googleLogo from './assets/google_logo.jpg';


const { Content } = Layout;
const { Title, Text } = Typography;

interface Props {
  setViewer: (viewer: Viewer) => void;
}

export const Login = ({ setViewer }: Props) => {
  const client = useApolloClient();
  const [ logIn, { 
    data: LogInData, 
    loading: LogInLoading, 
    error: LogInError 
  }] = useMutation<LogInData, LogInVariables>(LOG_IN, {
    onCompleted: data => {
      if (data && data.logIn) {
        setViewer(data.logIn)
        sessionStorage.setItem("token", data.logIn.token);
        displaySuccessNotification("You've successfully logged in!");
      }
    }
  });

  const logInRef = useRef(logIn);

  useEffect(() => {
    const code = new URL(window.location.href).searchParams.get("code");
    if (code) {
      logInRef.current({
        variables: {
          input: { code }
        }
      })
    }
  }, [])

  const handleAuthorize = async () => {
    try {
      const { data } = await client.query<AuthUrlData>({
        query: AUTH_URL
      })
      window.location.href = data.authUrl;
    } catch {
      displayErrorMessage("Failed to log in");
    }
  }

  if (LogInLoading) {
    return (
      <Content className="log-in">
        <Spin size="large" tip="Logging you in" />
      </Content>
    )
  }

  const loginErrorBanner = LogInError ? (
    <ErrorBanner message="We weren't able to log you in. Please try again." />
  ) : null;

  if (LogInData && LogInData.logIn) {
    const { id: viewerId } = LogInData.logIn; 
    return <Redirect to={`/user/${viewerId}`} />
  }

  return (
    <Content className="log-in">
      {loginErrorBanner}
      <Card className="log-in-card">
        <div className="log-in-card__intro">
          <Title level={3} className="log-in-card__intro-title">
            <span role="img" aria-label="wave">
              👋
            </span>
          </Title>
          <Title level={3} className="log-in-card__intro-title">
            Log in to Enlighten Me Daily!
          </Title>
          <Text>Sign in with Google to start bookmarking quotes and getting daily emails!</Text>
        </div>
        <button className="log-in-card__google-button" onClick={handleAuthorize}>
          <img
            src={googleLogo}
            alt="Google Logo"
            className="log-in-card__google-button-logo"
          />
          <span className="log-in-card__google-button-text">Sign in with Google</span>
        </button>
        <Text type="secondary">
          Note: By signing in, you'll be redirected to the Google consent form to sign in
          with your Google account.
        </Text>
      </Card>
    </Content>
  );
}